//- NAVBAR -//

.logo{
  position: fixed;
  float: left;
  margin: 12px 36px;
  color:#111;
  font-weight: bold;
  font-size: 24px;
}

.since{
  position: fixed;
  float: left;
  margin: 45px 65px 16px;
  color:#111;
  font-size: 10px;
  font-family: 'Old Standard TT', serif;
}

nav {
  margin-top: 5em;
  z-index: 2;
  position: fixed;
  width: 100%;
}

nav ul {
  list-style: none;
  background:#fff;
  overflow: hidden;
  padding: 0 0 0 0;
  text-align: center;
  margin: 0 0 0 0;
}

nav.black ul {
  background: #fff;
}
nav ul li {
  text-align: center;
  display: inline-block;
  padding: 24px 20px 20px 30px;

}

nav ul li a:hover, nav ul li a:active, nav ul li a:visited {
  text-decoration: none;
  color:#333;
  font-size: 16px;
  transition: 0.5s;
}
nav ul li a{
  text-decoration: none;
  color:#333;
  font-size: 14px;
  transition: 0.5s;
  font-weight: 700;


}
.menu-icon{
  width: 100%;
  background: #fff;
  text-align: right;
  box-sizing: border-box;
  padding: 15px 24px;
  cursor: pointer;
  color: #000;
  display: none;
}

@media(max-width:700px){
  .logo{
    position: fixed;
    top: 0;
    margin-top: 12px;
  }
  .since{
    position: fixed;
    float: left;
    margin: -20px 65px 0px;

  }

  nav ul {
    max-height: 0px;
    background: #fff;
    padding-bottom: 3px;

  }

  nav.black ul{
    background: #fff;

  }
  .showing {
    max-height: 25em;
  }
  nav ul li {
    box-sizing: border-box;
    width: 100%;
    padding: 24px 20px 20px 30px;
    text-align: center;
  }

  .menu-icon{
    display: block;
  }
}
